body {
  background: #2c3e50; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #4ca1af,
    #2c3e50
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #4ca1af,
    #2c3e50
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
#app {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.1rem;
}
.main {
  margin: 20px auto;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
}
form {
  margin: 30px auto;
  display: flex;
  justify-content: space-between;
  width: 40%;
  flex-direction: row;
}
input {
  border: none;
  color: black;
  border-radius: 5px;
  width: 57%;
  padding: 10px;
  font-size: 1rem;
  min-width: 140px;
}
input:focus {
  outline: none;
}
#buttonType {
  border-radius: 10px;
  font-size: 1rem;
  width: 30%;
  min-width: 100px;
}
button {
  display: block;
  margin: 0 20px;
  border-radius: 10px;
  color: black;
}
button:hover {
  cursor: pointer;
  color: grey;
}
.weather_block {
  display: flex;
  color: white;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: capitalize;
}
.weather_block p {
  background: white;
  color: black;
  border-radius: 6px;
  text-align: center;
  padding: 10px;
}
